describe('FloatingButton', () => {
    let wrapper, props;
  
    beforeEach(() => {
      // Set up mock navigation.navigate function
      props = {
        navigation: {
          navigate: jest.fn()
        }
      };
  
      // Render the component
      wrapper = shallow(<FloatingButton {...props} />);
    });
  
    it('should call navigation.navigate with correct arguments when pressed', () => {
      // Simulate button press
      wrapper.simulate('press');
  
      // Assert that navigation.navigate was called with the correct arguments
      expect(props.navigation.navigate).toHaveBeenCalledWith('Detail', {item: data , screen :'Add Item'});
    });
  });
